.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.inputForm {
  margin: 0% 40% 0% 40%;
  text-align: center;
}

.weatherCard {
  margin: auto;
} 

.maincard {
  margin: 5% 20% 5% 20%;
  justify-content: center;
}

.weatherIcon {
  margin: auto;
  width: 5%;
}

.logo {
  margin-right: 5%;
}

#root {
  background-image: url(landscape.jpg);
  min-height: 100vh;
}

.footer {
  margin-top: 15%;
  margin: auto;
  padding-top: 12.5%;
}

.footer {
  color: #fff;
}
.footer a {
  color: #fff;
}



/* @media only screen and (min-width: 1200px) {
  .footer {
    padding-top: 19.9%;
  }
} */


@media only screen and (max-width: 1200px) {
  .inputForm {
  margin: 0% 15% 0% 15%;
  }
  .footer {
    padding-top: 60%;
  }
  .maincard {
    margin: auto;
    margin-top: 5%;
  }
}